
<template>
    <div class="cart">
        <div class="cart-wrap" v-show="showCart" transition="bounce">
            <div class="cart-header">
                <span class="title">购物车</span>
                <span class="close" v-tap="toggleShowCart"><i class="icon icon-close"></i></span>
            </div>
            <p class="no-product" v-show="!products.length"><i>空空如也，快去添加商品吧！~</i></p>
            <ul class="counter-height min-list">
                <li class="clearfix" v-for="p in products">
                    <span class="title" >[{{p.cid}}-{{p.fid}}-{{p.aid}}] {{ !p.aid ? p.title : p.parentTitle+'—（'+p.title+'）' }}</span>
                    <span class="price number f14" v-text="p.price | currency '￥'"></span>
                    <span class="counter">
                        <counter :product="p"></counter>
                    </span>
                    <span class="del hide">
                        <button v-tap="delProduct(p)">x 删除</button>
                    </span>
                </li>
            </ul>
        </div>
        <div class="total clearfix">
            <div class="info" v-tap="toggleShowCart">
                <span class="ret" :class="{'disabled': !products.length}"><i class="icon icon-add_shopping_cart"></i><em class="number f14 green" v-text="quantity"></em></span>
                <span class="price" v-text="total | currency '￥'"></span>
            </div>
            <button class="btnBuy" :class="{ 'disabled': !products.length}" :disabled="!products.length" v-tap="checkout">提交购物车</button>
        </div>
    </div>
</template>

<script>
    import Counter from './counter.vue'
    import { cutQuantity, addQuantity, delProduct, showMask, toggleCart } from '../vuex/actions.js'
    import { getCart, getShowCart } from '../vuex/getters.js'
    export default {
        components: { Counter },
        vuex: {
            getters: {
                products: getCart,
                showCart: getShowCart,
            },
            actions: {
                cutQuantity,
                addQuantity,
                delProduct,
                showMask,
                toggleCart,
            }
        },
        computed: {
            total: function () {
                return this.products.reduce((total, p) => {
                    return total + p.price * p.quantity
                }, 0)

            },
            quantity: function(){
                return this.products.reduce((total, p) => {
                    return total + p.quantity
                }, 0)
            }
        },
        methods: {
            toggleShowCart(){
                var self = this
                if(self.showCart) {
                    self.isShowCart = false
                    self.toggleCart(false)
                    self.showMask(false)
                }else{
                    self.isShowCart = true
                    self.toggleCart(true)
                    self.showMask(true)
                }
            },
            checkout(){
                this.$route.router.go({
                    name: 'result',
                    //params 和 query 可选
                    //params: { 'id': id },
                    //query: { ... },
                    append: true
                })
            }
        },
        ready(){
            let height = 0
            let ret = document.querySelector('.ret')
            const t = setInterval(() => {
                if (height >= 330) {
                    height = 0
                    //clearInterval(t);
                    //return;
                }
                height+=1;
                ret.style.backgroundPositionY = -height + 'px'
            }, 50);
        }
    }
</script>

<style lang="less">
.bounce-transition {
    display: block; /* 否则 scale 动画不起作用 */
}
.bounce-enter {
    animation: bounce-in .35s;
}
.bounce-leave {
    animation: bounce-out .35s;
}
@keyframes bounce-in {
    0% { transform: translateY(100%);}
    100% { transform: translateY(0);}
}
@keyframes bounce-out {
    0% {transform: translateY(0);}
    100% {transform: translateY(100%);}
}
.element-animation{
    animation: animationFrames linear 0.8s;
    animation-iteration-count: 1;
    transform-origin: 50% 50%;
}
@keyframes animationFrames{
    0% {
        transform:  translate(0px,0px)  ;
    }
    15% {
        transform:  translate(0px,-10px)  ;
    }
    30% {
        transform:  translate(0px,0px)  ;
    }
    75% {
        transform:  translate(0px,-5px)  ;
    }
    100% {
        transform:  translate(0px,0px)  ;
    }
}
</style>


