<template>
    <div class="attr combo" transition="wcy-bounce">
        <div class="name">[{{product.fid}}]{{product.title}}</div>
        <ul class="combo-list counter-height">
            <li class="clearfix" v-for="p in combo">
                <dl>
                    <dt class="title">[{{p.cid}}-{{p.fid}}-] {{ p.title }}</dt>
                    <dd v-for="t in p.item"
                    :class="{ 'active':  }"
                    v-tap="selectCombo(t.type, $index, $event)">
                        [{{t.fid}}]-{{t.title}}
                    </dd>
                </dl>
            </li>
        </ul>
        <div class="btn clearfix">
            <button class="btn-cancel" v-tap="showCart">取消</button>
            <button class="btn-entry" v-tap="showCart">确定</button>
        </div>

    </div>
</template>

<script>
    import { showCart, setCombo } from '../vuex/actions.js'
    import { getProduct, getCombo } from '../vuex/getters.js'
    export default {
        methods: {
            selectCombo(type, index, event){
                if(type === '1' ){
                    console.log('单选');
                }else if(type === '2') {
                    console.log('多选');
                }
            },
        },
        vuex: {
            getters: {
                product: getProduct,
                combo: getCombo,
            },
            actions: {
                showCart, setCombo,
            }
        },
        ready() {
            this.setCombo(this)
        },
    }
</script>





