<template>
    <div class="picker-address" data-address="{{value | array2string}}">
        <div class="item clearfix" @click="onClick">
            <label for="address">{{title}}：</label>
            <span class="right picker-address-value" v-if="!showName && value.length">{{value | array2string}}</span>
            <span class="right picker-address-name" v-else="showName && value.length">{{value | value2name list}}</span>
            <span class="right picker-address-placeholder" v-if="!value.length && placeholder" v-html="placeholder"></span>
        </div>
        <picker :show.sync="showPicker" :data="address" :columns="columns" :value.sync="value" ></picker>

        <div class="mask picker-mask" v-show="showPicker" v-tap="showPicker=false"></div>
    </div>
</template>

<script>
import Picker from './picker.vue'
export default {
    components: {Picker},
    props: {
        title: {
            type: String,
            default: '地址',
        },
        list: {
            type: Array,
        },
        value: {
            type: Array,
            twoWay: true
        },
        columns: {
            type: Number,
        },
        placeholder: String,
    },
    data(){
        return {
            showPicker: false,
            showName: true,
            address: [],
        }
    },
    methods: {
        onClick(){
            let self = this
            self.showPicker = true
            let shen = [], shi = [], qu = [];
            self.address = []

            if(self.value.length){
                self.list.forEach(function(v){
                    if(v.parent===undefined) {
                      //  console.log("======shen=======",v)
                        shen.push(v)
                    }
                    if(self.value.length && self.value[0] === v.parent){
                        shi.push(v)
                    }
                    if(self.value.length && self.value[1] === v.parent ){
                        qu.push(v)
                    }
                })
            } else {
                self.list.forEach(function(v){
                    if(v.parent===undefined) {
                        shen.push(v)
                    }
                    if(shen[0].value === v.parent) {
                        shi.push(v)
                    }
                    if(shi.length && shi[0].value === v.parent) {
                        qu.push(v)
                    }
                })
            }
            self.address.push(shen)
            self.address.push(shi)
            self.address.push(qu)
        },
    },
    events: {
        'on-change': function (val, index) {
            console.log("传入的参数：",val, index)
            this.address[index+1] = this.list.filter(function(v){
                return v.parent === val
            })
        },
    },
}
</script>

<style lang="less" scope>
.right {
    font-size: 1.4rem;
    padding-right: 1rem;
}
.picker-address-placeholder {color:#A9A9A9;}
.mask {
    position: fixed;
    z-index: 1000;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .5);
}
</style>



